.container{
    height: auto;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: stretch;
}

*{
    font-family: Arial, Helvetica, sans-serif;
    margin: 0px;
    padding: 0px;
}

.container-form{
    width: 500px;
    padding: 20px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}

.container-form header{
    margin-top: 2%;
    align-items: center;
}

.container-img{
    width: 800vh;
    height: 100vh;
    display: flex;
    background-image: url(../../images/RegisterFondo.png);
    background-size: auto;
    background-position: 0px, 800px;
    background-repeat: no-repeat;
    background-color: #bca4dc;
}


#form-register{
    display: flex;
    margin-top: 50%;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: center;
}

#form-register input{
    width: 200px;
    height: 30px;
    padding: 2%;
    margin-top: 7%;
    border: solid rgb(229, 232, 232) 2px;
    border-radius: 10px;
}

#form-register button{
    width: 200px;
    height: 40px;
    margin-top: 7%;
    background-color:rgb(0, 97, 255);
    cursor: pointer;
    color: white;
    font-size: 18px;
    border: none;
    border-radius: 15px;
}

#form-register div{
    margin-top: 5%;
}



